          <a class="font-square-bottom">此功能模块<br>正在维护中！</a>
        </div>
        <div class="col-xs-1 square">
          <div class="font-square-top">测评中心</div>
          <a class="font-square-bottom">此功能模块<br>正在维护中！</a>
        </div>
        <div class="col-xs-1 square">
          <div class="font-square-top">实习兼职</div>
          <a class="font-square-bottom">此功能模块<br>正在维护中！</a>
        </div>
        <div class="col-xs-3 post-training">
          <div class="font-square-top">岗位训练</div>
          <a class="font-square-bottom">此功能模块<br>正在维护中！</a>
        </div>
        <div class="col-xs-1 square" @click="checkGo('/member/login', '_blank')">
          <div class="font-square-top">砺英社区</div>
          <a class="font-square-bottom" >点击进入<br>砺英社区！</a>
        </div>
      </div>
      <!--      </div>-->

      <div class="row" style="margin-left: 5%;width: 95%">
        <div class="task-list col-xs-8" v-if="taskData.length !== 0">
          <div>
            <div class="task" v-for="obj in taskData" v-bind:key="obj.taskCode">
              <span class="daily-clock">{{obj.taskName}}</span>
              <i class="iconfont icongerenshouye-naozhong1 task-time"></i><span class="task-time">{{obj.updateTime}}</span>
              <div style="height:1px;background:rgba(60,40,110,1);width: 100%;float: left;margin-top: 8px;"></div>
              <span class="task-detail" v-if="obj.activeDescription === null || obj.activeDescription === ''">{{obj.taskDescription}}</span>
              <span class="task-detail" v-else>{{obj.activeDescription}}</span>
              <span class="task-detail">奖砺：积分值<span style="color: #FF8717">+{{obj.taskScore}}</span></span>
              <span class="to-complete" @click="goUrl(obj)">去完成</span>
              <span class="divcss5"></span>
            </div>
            <div class="task1" v-for="(lab,index) in taskData1" v-show='index < 4 - taskData.length' v-bind:key="index">
              <span class="daily-clock"></span>
              <span class="task-time"></span>
              <div style="height:1px;width: 100%;float: left;margin-top: 8px;"></div>
              <span class="task-detail"></span>
              <span class="task-detail"></span>
              <span class="to-complete1"></span>
              <span class="divcss51"></span>
            </div>
