          <span class="today-integral-number">暂无</span>
          <span class="today-integral-tishi">完成每日任务既可以提升能力又能获得积分</span>
        </div>
        <div class="message col-xs-4">
          <div class="font-square-top today-integral" style="margin-top: 0;font-size:16px;font-family:PingFang-SC-Medium;font-weight:500;color: #A6CBFE;width: 85%;line-height: 62px;text-align: left;height: 62px;margin-left: 55px;">系统公告</div>
          <div style="margin-top: 60px;margin-left: 10%;width: 85%;padding: 2px 0px;max-height: 186px;overflow: auto;overflow-x: hidden;">
            <div v-for="(item, key) in materialTypes" v-bind:key="key" @click="showStail(item)" style="margin-top: 16px;cursor: pointer;border-bottom: 1px solid rgba(173,185,214,0.2);padding-bottom: 5px;" class="row">
