<script>
export default {
  name: 'app',
  mounted () {
    this.loadData()
  },
  data () {
    return {
      userInfo: null,
      taskData: [],
      userIsCompleted: true,
      materialTypes: [],
      taskData1: [{

      }, {

      }, {

      }, {

      }, {

      }, {

      }]
    }
  },
  methods: {
    loadData () {
      let me = this
      me.$nextTick(function () {
        me.document = document.getElementById('echart')
        me.drawLine()// => 'updated'
      })
      this.$http.post('/user/queryuserinfo', {}, res => {
        if (res.success) {
          // console.log(res)
          me.userInfo = res.data
          localStorage.setItem('userEnterYear', me.userInfo.userEnterYear)
          localStorage.setItem('userCode', me.userInfo.userCode)
        }
      })
      me.$http.post('/homepage/queryplatformtasklist', {}, res => {
        console.log(res)
        if (res.success) {
          me.taskData = res.data
          for (var i = 0; i < me.taskData.length; i++) {
            if (me.taskData[i].taskCode === '1001') {
              me.userIsCompleted = false
            }
          }
        }
      })
      me.$http.post('/doproject/noticeInfo', {
      }, res => {
        console.log(res)
        if (res.success) {
          if (res.data !== null) {
            for (let obj of res.data) {
              me.materialTypes.push({
                id: obj.id,
                kind: '系统公告',
                time: obj.createTime,
                content: obj.notice,
                url: obj.url,
                status: ''
              })
            }
          }
        }
      })
    },
    goUrl (obj) {
      if (this.userIsCompleted === false && obj.taskCode !== '1001') {
        this.$alert('请先完善个人信息！', '提示', {
          confirmButtonText: '确定'
        })
        return false
      } else if (obj.taskCode === '1002') {
        /* 组队任务 */
        // let teamId = localStorage.getItem('teamId')
        // if (teamId === '' || teamId === 'undefined' || teamId === 'null' || teamId === null) {
        //   window.location = 'school-main.html'
        // } else {
        //   window.location = 'task-list.html'
        // }
        if (obj.teamId === null && obj.activeUrl === null && obj.activeDescription === null) {
          this.$alert('请先选择项目再进行组队！', '提示', {
            confirmButtonText: '确定',
            callback: action => {
              window.location = 'school-main.html'
            }
          })
        } else {
          this.$http.post('/teambuild/teamIdentityInfo', {
            teamId: obj.teamId
          }, respersonal => {
            localStorage.setItem('myJoinStatus', respersonal.data.joinStatus)
            localStorage.setItem('myJoinType', respersonal.data.joinType)
            localStorage.setItem('teamId', obj.teamId)
            localStorage.setItem('courseName', obj.courseName)
            localStorage.setItem('projectId', obj.projectId)
            localStorage.setItem('courseId', obj.courseId)
            localStorage.setItem('grade', obj.grade)
            window.location = obj.activeUrl
          })
        }
      } else if (obj.taskCode === '1003') {
        // 完成项目任务
        // if (obj.teamId === '' || obj.teamId === 'undefined' || obj.teamId === 'null' || obj.teamId === null) {
        if (obj.projectId === '' || obj.projectId === 'undefined' || obj.projectId === 'null' || obj.projectId === null) {
          this.$alert('请先完成组队任务！', '提示', {
            confirmButtonText: '确定'
          })
        } else {
          localStorage.setItem('teamId', obj.teamId)
          localStorage.setItem('projectId', obj.projectId)
          window.location = obj.taskUrl
        }
      } else {
        window.location = obj.taskUrl
      }
    },
    setting () {
      window.location = 'personal-set.html'
    },
    checkGo (url, target) {
      if (this.userIsCompleted === false) {
        this.$alert('请先完善个人信息！', '提示', {
          confirmButtonText: '确定'
        })
        return false
      } else {
        if (target === '_blank') {
          // this.$axios({
          //   method: 'post',
          //   url: 'http://localhost:8080/member/login',
          //   data: { name: localStorage.getItem('userPhone') },
          //   transformRequest: [data => {
          //     let ret = ''
          //     for (let it in data) {
          //       ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          //     }
          //     return ret
          //   }],
          //   headers: {
          //   }
          // }).then(
          //   (response) => {
          //     console.log(response)
          //     window.open('http://localhost:8080' + response.data.url, target)
          //   }
          // ).catch(
          //   (error) => {
          //     console.log(error)
          //   }
          // )
          // 测试社区注册接口
          // this.$axios({
          //   method: 'post',
          //   url: 'http://localhost:8080/member/register',
          //   data: {
          //     name: 'yuziboceshi',
          //     phone: '13224135685',
          //     password: '111111',
          //     repassword: '111111'
          //   },
          //   transformRequest: [data => {
          //     let ret = ''
          //     for (let it in data) {
          //       ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          //     }
          //     return ret
          //   }],
          //   headers: {
          //   }
          // }).then(
          //   (response) => {
          //     console.log(response)
          //     // window.open('http://localhost:8080/member/', target)
          //   }
          // ).catch(
          //   (error) => {
          //     console.log(error)
          //   }
          // )
          // window.open(this.$http.snsUrl + url + '?' + 'snsNo=' + window.btoa(window.encodeURIComponent(localStorage.getItem('snsNo'))) + '&redirectUrl=/group/topic/4', target)
          if (localStorage.getItem('canLoginSns') === '1') {
            this.$alert('社区服务维护中！请联系管理员！', '提示', {
              confirmButtonText: '确定'
            })
            return false
          }
          if (this.userInfo.userNickname === '' || this.userInfo.userNickname == null || this.userInfo.userNickname === undefined) {
            this.$alert('为了您的体验，请先前往个人设置设置昵称!', '提示', {
              confirmButtonText: '确定'
            }).then(({ value }) => {
              window.location = 'personal-set.html'
            })
            return false
          }
          window.open(this.$http.snsUrl + url + '?' + 'snsNo=' + window.btoa(window.encodeURIComponent(localStorage.getItem('snsNo'))), target)
          // let temp_form = document.createElement('form')
          // temp_form.action = this.$http.snsUrl + '/member/login'
          // temp_form.target = '_blank'
          // temp_form.method = 'post'
          // temp_form.style.display = 'none'
          // var hideInput = document.createElement('input')
          // hideInput.type = 'hidden'
          // hideInput.name = 'name'
          // hideInput.value = localStorage.getItem('snsNo')
          // temp_form.appendChild(hideInput)
          // document.body.appendChild(temp_form)
          // temp_form.submit()
          // document.body.removeChild(temp_form)
        } else {
          window.open(url, target)
        }
      }
    },
    drawLine () {
      // 雷达图
      var myChart = this.$echarts.init(this.document)
      // 绘制图表
      myChart.setOption({
        title: {
          // text: '能力对比'
        },
        tooltip: {
          trigger: 'axis'
        },
        grid: { // 控制图的大小，调整下面这些值就可以，
          x: 0,
          y: 0,
          x2: 0,
          y2: 0
        },
        radar: [
          {
            indicator: [
              { text: 'Java', max: 100 },
              { text: '前端', max: 100 },
              { text: '测试', max: 100 },
              { text: '部署', max: 100 },
              { text: '软件工程技术', max: 100 }
            ],
            center: ['50%', '45%'],
            radius: 80,
            name: {
              textStyle: {
                color: 'white'
              }
            },
            splitLine: {
              lineStyle: {
                color: [
                  'rgba(53,71,248,1)', 'rgba(53,71,248,0.8)',
                  'rgba(53,71,248,0.6)', 'rgba(53,71,248,0.4)',
                  'rgba(53,71,248,0.2)', 'rgba(53,71,248,0.1)'
                ].reverse()
              }
            },
            splitArea: {
              show: false
            },
            axisLine: {
              lineStyle: {
                color: '#781CEE'
              }
            }
          }
        ],
        series: [
          {
            type: 'radar',
            tooltip: {
              trigger: 'item'
            },
            data: [
              {
                value: [60, 73, 85, 40, 90],
                name: '第一次',
                areaStyle: {
                  normal: {
                    color: '#0D5EF3' // 选择区域颜色
                  }
                },
                lineStyle: {
                  color: 'rgba(53,71,248,0.8)'
                }
              }
              // {
              //   value: [70, 63, 65, 60, 100],
              //   name: '第二次',
              //   areaStyle: {
              //     normal: {
              //       color: '#781CEE' // 选择区域颜色
              //     }
              //   }
              //
              // }
            ]
          }
        ]
      })
    },
    showStail (item) {
      localStorage.setItem('urlContent', item.url)
      window.open('notice.html?1')
      // window.location = 'notice.html'
      // window.location = item.url
    }
  }
}
</script>

